#preview-image {
  width: 90%;
  margin: 1% auto;
}
#preview-image img {
  max-width: 100%;
  /* padding: 1%; */
}

.ProfileCard {
  width: 300px;
  border: 2px solid #ccc;
  box-shadow: 10px 10px 10px #cccc;
  position: fixed;
  top: 100px;
  left: 40%;
  border-radius: 20px;
  padding: 10px;
  background-color: white;
}
.ProfileCard .inputContainer {
  padding: 0px 1px;
}
.ProfileCard button {
  background-color: white;
  color: #4e94f4;
  border: 2px solid #4e94f4;
  width: 60px;
  border-radius: 10px;
  /* margin-left: 10%; */
}
